.about-me {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  width: 90%;
  margin: 0 auto;
  height: 80vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.about-me__left {
  width: 60%;
  height: auto;
  border: 2px solid #f37771b0;
  margin-right: 10px;
}

.about-me__right {
  width: 40%;
  height: 100%;
}

.about-me__text {
  padding: 25px;
  color: #a09090f8;
  margin: 0;
  font-size: 18.3px;
  letter-spacing: 0;
}

.about-me__text_transition-appear {
  opacity: 0;
}

.about-me__text_transition-appear.about-me__text_transition-appear-active {
  opacity: 1;
  -webkit-transition: 2.5s ease-in-out;
  -o-transition: 2.5s ease-in-out;
  transition: 2.5s ease-in-out;
}

.about-me__button-active {
  background-color: transparent;
  border: 1px solid white;;
  color: #f37771b0;
  font-size: 20px;
  font-family: 'Montserrat';
  width: 130px;
  height: 200px;
  -webkit-animation: shakethat 6.5s infinite;
          animation: shakethat 6.5s infinite;
}

.about-me__button-active:hover {
  -webkit-animation: none;
          animation: none;
}

.about-me__button-disactive {
  display: none;
}

@-webkit-keyframes shakethat {
  20% {-webkit-transform: rotateZ(10deg);transform: rotateZ(10deg);}
  40% {-webkit-transform: rotateZ(-10deg);transform: rotateZ(-10deg);}
  60% {-webkit-transform: rotateZ(10deg);transform: rotateZ(10deg);}
  80% {-webkit-transform: rotateZ(-10deg);transform: rotateZ(-10deg);}
  100% {-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg);}
}

@keyframes shakethat {
  20% {-webkit-transform: rotateZ(10deg);transform: rotateZ(10deg);}
  40% {-webkit-transform: rotateZ(-10deg);transform: rotateZ(-10deg);}
  60% {-webkit-transform: rotateZ(10deg);transform: rotateZ(10deg);}
  80% {-webkit-transform: rotateZ(-10deg);transform: rotateZ(-10deg);}
  100% {-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg);}
}


/*****************************************/
@media (max-width: 1630px) {
  .aboutLeft>p {
    font-size: 22px;
  }
}

@media (max-width: 1440px) {
  .aboutLeft>p {
    font-size: 20px;
  }
}


/* **************************************** */
@media (max-width: 1164px) {
  .aboutLeft>p {
    font-size: 18px;
  }
}

/* **************************************** */
@media (max-width: 1024px) {
  .aboutLeft>p {
    font-size: 17px;
  }
}

