.navigation {
  margin: 0;
  width: 100%;
  height: 100vh;
}

.navigationList {
  margin: 0;
  padding: 25px 0 20px;
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 10vh;
}

.listItem>a {
  font-size: 27px;
  color: #6e869c;
  text-decoration: none;
}

.listItem>a:hover, .active {
  color: #0844a4;
  font-weight: bold;
} 

.active {
  letter-spacing: 3px;
}




