.contact {
  margin: 0;
  width: 100%;
  height: 80vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

.contact__header {
  margin-bottom: 50px;
  font-size: 50px;
  letter-spacing: 2.5px;
  color: #94a9bd;
}

.contact__links {
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.contact__fficial, .contact__social, .contact__other {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 20px;
}

.contact__official>p {
  margin: 30px 0 0;
  color: #81a0bd;
  -webkit-transition: .8s;
  -o-transition: .8s;
  transition: .8s;
}

.contact__official>a, .contact__other>a, .contact__social>a {
  margin-top: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: #81a0bd;
  text-decoration: none;
  -webkit-transition: .8s;
  -o-transition: .8s;
  transition: .8s;
}

.contact__official>a:hover, .contact__other>a:hover, .contact__social>a:hover {
  color: #e48480;  
}

.contact__official>p:hover {
  color: #e48480;  
}

.contact__official>h2, .contact__other>h2, .contact__social>h2 {
  text-align: center;
  letter-spacing: 2px;
  color: #e48480;
} 

.contact__icon {
  margin-right: 20px;
  font-size: 40px;
  color:#81a0bd;
}

.contact__official_transition-appear {
  opacity: 0;
  -webkit-transform: translateY(100px);
      -ms-transform: translateY(100px);
          transform: translateY(100px);
}

.contact__official_transition-appear-active {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all 0.6s ease-in-out;
  -o-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.contact__social_transition-appear {
  opacity: 0;
  -webkit-transform: translateY(-100px);
      -ms-transform: translateY(-100px);
          transform: translateY(-100px);
}

.contact__social_transition-appear.contact__social_transition-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: all 0.6s ease-in-out;
  -o-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.contact__other_transition-appear {
  opacity: 0;
  -webkit-transform: translateY(100px);
      -ms-transform: translateY(100px);
          transform: translateY(100px);
}

.contact__other_transition-appear.contact__other_transition-appear-active {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all 0.6s ease-in-out;
  -o-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}


